// 
// Full line
// 

.onboarding-progress-container {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  margin-left: clamp(0px, 10vw, 80px);
  margin-right: clamp(0px, 10vw, 80px);
  background: $black;

  @media (max-width: 500px) {
    margin-right: 8px;
    margin-left: 8px;
  }
}

.current-progress {
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, $primary 0%, $primary 50%, transparent 50%, transparent 100%);
  background-position-x: 150px;
  background-size: 300px;

  transition: all 0.3s ease-out;
}

.current-progress.filled {
  background-position-x: 300px;
  color: $white;
  box-shadow: 0px 0px 18px rgba($primary, 1);
}
