@import './../../styles/variables.scss';

.extension-item{
  position: relative;
  background: $black;
  margin-top: clamp(20px, 8vw, 80px);
  cursor: pointer;
  border-radius: 15px;
  user-select: none;
  transition: all 0.4s ease-out;

  .completed{
    transition: all 0.4s ease-out;
  }

  &:hover{
    background: $gray;

    .completed{
      // box-shadow: 0px 4px 32px rgba($primary, 0.5);
      box-shadow: 0px 4px 32px rgba(0, 0, 0, 0.6);
    }
  }

  .step-counter{
    color: $primary;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 100%;
    font-size: clamp(2.5em, 10vw, 4.5em);
    width: clamp(65px, 15vw, 100px);
    height: clamp(65px, 15vw, 100px);
    line-height: clamp(60px, 14vw, 90px);
    font-weight: 700;
    text-align: center;
    user-select: none;
    
    background: linear-gradient(86.63deg, #FFFFFF 5.57%, #67E646 51.85%, #26BA3F 94.43%);
    background-position-x: 0px;
    background-size: 700px;

    transition: background 0.8s ease-out, color 0.1s ease-out;
  }

  .step-counter.completed{
    background-position-x: -520px;
    color: $white;
  }

  .step-content{
    padding-top: clamp(10px, 10vw, 24px);
    padding-right: clamp(50px, 10vw, 82px);
    padding-left: clamp(50px, 10vw, 82px);
    padding-bottom: clamp(10px, 10vw, 24px);
    border-radius: 15px;
  }

  .step-logo{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(50%, -50%);
    height: clamp(40px, 18vw, 140px);
  }

}